body {
  font-family: 'Raleway', Tahoma, Arial, sans-serif;
}

.container {
  max-width: 1055px;
  margin: 0 auto;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.row {
  display: flex;
  justify-content: space-between;
}

.section_title {
  font-size: 22px;
  color: #666;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 78px;
  margin-top: 45px;
}

.section_title::after {
  content: "";
  width: 320px;
  height: 7px;
  background: #27c7ab;
  display: flex;
  margin-top: 5px;
}

.btn {
  background: #27c7ab;
  color: #fff;
  border-radius: 10px;
  border: 0;
  padding: 15px 45px;
  text-transform: uppercase;
  cursor: pointer;
  display: inline-block;
  outline: none;
}

.btn:hover {
  background: #27b499;
}

.page_header {
  height: 40px;
  background: #000;
  position: relative;
  z-index: 10000;
}

.page_header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.page_logo {
  margin: 0;
  display: flex;
  align-items: center;
}

.page_logo_link {
  text-transform: uppercase;
  font-size: 15px;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.page_logo_link span {
  color: #27c7ab;
  font-weight: 700;
}

.page_nav_list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.page_nav_list_el {
  height: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page_nav_list_link {
  display: block;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  padding: 20px 0px 20px 33px;
  transition: 0.3s color;
}

.page_nav_list_link:hover {
  color: #27c7ab;
}

.page_nav_list_el:nth-child(2) a {
  padding-left: 50px;
}

.page_nav_sublist::before {
  content: "";
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-bottom-color: #000;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translate(-50%);
}

.page_nav_sublist::after {
  content: "";
  width: 100%;
  height: 12px;
  background: transparent;
  position: absolute;
  left: 0;
  top: -12px;
}

.page_nav_sublist {
  background: #000;
  position: absolute;
  top: calc(100% + 12px);
  left: calc(50% + 20px);
  transform: translate(-50%);
  margin: 0;
  padding: 10px 15px;
  list-style: none;
  visibility: hidden;
  opacity: 0;
  transition: 0.5s opacity;
}

.page_nav_list_el:hover .page_nav_sublist {
  opacity: 1;
  visibility: visible;
}

.page_nav_sublist_link {
  display: block;
  color: #fff;
  text-decoration: none;
  padding: 3px;
}

.main_slider_bg {
  background: linear-gradient(#ffffff, #dddddd);
}

.slider {
  height: 505px;
  display: flex;
}

.slider_prev,
.slider_next {
  width: 50px;
  border: 0;
  background: none;
  color: #27c7ab;
  font-size: 70px;
  cursor: pointer;
  outline: none;
}

.slider_prev:focus,
.slider_next:focus {
  color: #000000;
}

.slider_next {
  order: 1;
}

.slider_slides {
  flex-grow: 1;
  position: relative;
}

.slider_slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 1s opacity;
  opacity: 0;
}

.slider_slide_active {
  opacity: 1;
  z-index: 1;
}

/* slajdy*/
.slider_slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider_slide_img_cnt {
  width: 400px;
  height: 350px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.slider_slide_img {
  width: 100%;
  height: auto;
}

.slider_slide:nth-child(3) .slider_slide_img {
  width: 90%;
  height: auto;
}

.slider_slide:nth-child(3) .slider_slide_img_cnt {
  margin-top: 10px;
}

.slider_slide_text {
  flex-grow: 1;
  margin-top: 60px;
  margin-bottom: 60px;
  margin-right: 95px;
  height: 350px;
  text-align: center;
}

.slider_slide_text h1,
.slider_slide_text h5 {
  text-transform: uppercase;
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 30px;
  margin-top: 60px;
}

.slider_slide_text span {
  color: #27c7ab;
  font-weight: 700;
}

.slider_slide_text p {
  font-size: 13px;
  line-height: 22px;
}

.slider_slide .btn {
  margin-top: 40px;
  margin-bottom: 80px;
  font-size: 22px;
  font-weight: 600;
}

.chairs {
  margin-bottom: 67px;
}

.box {
  position: relative;
  margin-top: 28px;
}

.box_title {
  position: absolute;
  left: 0;
  bottom: 5px;
  font-size: 16px;
  color: black;
  height: 60px;
  width: 100%;
  background-color: rgba(230, 230, 230, 0.4);
  padding-left: 15px;
  padding-top: 15px;
}

.box_title span {
  text-transform: uppercase;
}

.box_title ::after {
  content: "";
  width: 138px;
  height: 7px;
  background: #27c7ab;
  display: block;
}

.box_article {
  width: 345px;
  height: 255px;
  border: 1px solid #DBDBDB;
  margin-top: 28px;
  padding: 10px 25px;
  font-size: 14px;
  line-height: 1.4em;
}

.box_article_title {
  font-size: 16px;
  color: black;
}

.box_article_title::after {
  content: "";
  width: 138px;
  height: 7px;
  background: #27c7ab;
  display: flex;
}

.box_article p:nth-of-type(2) {
  margin-top: 26px;
}

.price {
  background-color: #F1F1F1;
  overflow: hidden;
}

.price_box,
.price_box--green {
  position: relative;
  width: 330px;
  height: 500px;
  border: 2px solid #D5D5D5;
  background-color: #F9F9F9;
  text-align: center;
  margin-bottom: 65px;
}

.price_box_title {
  text-transform: uppercase;
  font-size: 22px;
  color: #27c7ab;
  font-weight: 600;
  margin-top: 26px;
}

.price_box_cnt {
  width: 130px;
  height: 130px;
  border: 2px solid #D5D5D5;
  border-radius: 50%;
  color: #C2C2C2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  background-color: #FFFFFF;
  margin: 0 auto 32px;
}

.price_box_cnt p:nth-child(1) {
  font-size: 42px;
  margin: 0 0 5px 5px;
}

.price_box_cnt span {
  font-size: 16px;
  vertical-align: text-top;
}

.price_box_cnt p:nth-child(2) {
  margin: 0;
  text-transform: uppercase;
}

.price_box_paragraph_first,
.price_box_paragraph_second,
.price_box_paragraph_third,
.price_box_paragraph_fourth {
  margin: 0;
  font-size: 24px;
  color: #939393;
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.price_box_paragraph_first,
.price_box_paragraph_third {
  background-color: #E4E4E4;
}

.price_box_paragraph_second,
.price_box_paragraph_fourth {
  background-color: #F1F1F1;
}

.price_box_btn {
  position: absolute;
  bottom: 18px;
  left: calc(50% - 60px);
  padding: 0px;
  height: 35px;
  width: 120px;
}

.price_box--green {
  background-color: #27c7ab;
  border-color: #27c7ab;
}

.price_box--green .price_box_title {
  color: #ffffff;
}

.price_box--green .price_box_cnt {
  color: #27c7ab;
  border: 2px solid #27c7ab;
}

.price_box--green .price_box_paragraph_first,
.price_box--green .price_box_paragraph_third {
  background-color: #FFFFFF;
}

.price_box--green .price_box_btn {
  background-color: #ffffff;
  color: #27c7ab;
}

.price_box--green .price_box_btn:hover {
  background-color: #e8e8e8;
}

.price_box::after,
.price_box--green::after {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  content: "";
  width: 54px;
  height: 54px;
  border-radius: 50%;
}

.price_box::after {
  background-color: #C2C2C2;
  border: 2px solid #C2C2C2;
  box-shadow: inset 0 0 0 15px #ffffff;
}

.price_box--green::after {
  background-color: #27c7ab;
  border: 2px solid #27c7ab;
}

.price_box--green::before {
  display: block;
  content: "";
  position: absolute;
  top: -25px;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  width: calc(58px - 30px);
  height: calc(58px - 30px);
  background-color: #ffffff;
  border-radius: 50%;
}

.application {
  background-color: #f9f9f9;
  padding: 30px 0;
}

.application .section_title {
  font-size: 20px;
}

.section_title::after {
  width: 330px;
}

.application header.align_right {
  text-align: right;
}

.image_part {
  width: 395px;
}

.summary_part,
.choice_part {
  width: 330px;
}

.application_form {
  margin-top: 50px;
}

.drop_down_list {
  border: 1px solid #8de0d2;
  width: 330px;
  height: 46px;
  background-color: white;
  margin-top: 22px;
  padding: 8px 12px;
  position: relative;
}

.list_label {
  font-size: 30px;
  color: #e2e2e2;
}

.application_checkbox {
  position: relative;
  padding: 30px 30px 30px 0;
}

.application_checkbox input {
  display: block;
  opacity: 0;
  height: 48px;
  position: absolute;
  width: 48px;
  cursor: pointer;
}

.application_checkbox label {
  display: block;
  padding-left: 65px;
  position: relative;
  font-size: 28px;
  color: #cecece;
  line-height: 120%;
  padding-top: 10px;
  cursor: pointer;
}

.application_checkbox label::before,
.application_checkbox label::after {
  border: 1px solid #27c7ab;
  content: "";
  display: block;
  left: 0;
  height: 48px;
  position: absolute;
  width: 48px;
  top: 0;
}

.application_checkbox label::after {
  background: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20xmlns%3Axlink%3D%22http%3A//www.w3.org/1999/xlink%22%20preserveAspectRatio%3D%22xMidYMid%22%20width%3D%2233.875%22%20height%3D%2233%22%20viewBox%3D%220%200%2033.875%2033%22%3E%0A%20%20%3Cpath%20d%3D%22M9.470%2C15.209%20L14.061%2C24.104%20C14.061%2C24.104%2021.522%2C6.026%2033.287%2C-0.000%20C33.000%2C4.304%2031.852%2C8.035%2033.861%2C12.626%20C28.696%2C13.774%2018.078%2C26.687%2014.635%2C33.000%20C9.756%2C26.974%204.017%2C22.382%20-0.000%2C20.948%20L9.470%2C15.209%20Z%22%20fill%3D%22%2327c7ab%22/%3E%0A%3C/svg%3E%0A") no-repeat center;
  border: 0;
  display: none;
}

.application_checkbox input:checked + label::after {
  display: block;
}

.inline_text {
  display: inline-block;
  width: 228px;
  vertical-align: top;
  margin-top: 30px;
  margin-left: 15px;
  color: #aeaeae;
}

.image_part img {
  margin-top: 140px;
  margin-left: calc(395px / 2 - 264px / 2);
}

.summary_panel {
  background-color: white;
  width: 100%;
  height: 195px;
  padding: 10px;
  margin-top: 50px;
}

.application_btn {
  text-align: center;
  width: 200px;
  padding: 10px 30px;
  font-size: 24px;
  margin-top: 24px;
  margin-left: calc(330px / 2 - 200px / 2);
  font-weight: 600;
}

.panel_left {
  display: inline-block;
  width: 185px;
  vertical-align: top;
  padding-bottom: 10px;
  border-bottom: 1px solid #e7e7e7;
  height: 135px;
}

.panel_right {
  display: inline-block;
  width: 100px;
  border-left: 1px solid #e7e7e7;
  text-align: right;
  vertical-align: top;
  padding-bottom: 10px;
  border-bottom: 1px solid #e7e7e7;
  height: 135px;
