.title {
  color: #24baa0;
  font-size: 22px;
  margin-top: 10px;
  font-weight: 600;
  list-style: none;
  height: 30px;
  margin-bottom: 10px;
}

.panel_left span {
  margin-top: 10px;
  font-size: 22px;
  color: #5b5a5a;
  display: block;
}

.panel_right span {
  display: block;
  font-size: 22px;
