  top: 45px;
  display: none;
}

.list_panel li {
  margin-top: 10px;
  cursor: pointer;
  color: #585858;
}

.main_contact .section_title {
  margin-top: 32px;
  margin-bottom: 48px;
}

.form {
  display: flex;
  justify-content: space-between;
}

.form_left {
  width: 635px;
}

.form_right {
  width: 360px;
}

.form_row {
  margin-bottom: 26px;
}

.form input[type="text"],
.form input[type="email"],
.form textarea {
  height: 65px;
  border: 1px solid #27c7ab;
  width: 100%;
  padding: 0 15px;
  font-size: 33px;
  font-weight: 200;
  outline: none;
}

.form input[type="text"]:focus,
.form input[type="email"]:focus,
.form textarea:focus {
  box-shadow: inset 0 0 0 1px #27c7ab;
}

.form input[type="text"]::placeholder,
.form input[type="email"]::placeholder,
.form textarea::placeholder {
  color: #aaa;
}

.form textarea {
  padding: 15px;
  height: 190px;
  resize: none;
}

.form_img {
  height: 210px;
  background-image: url("../../images/red_chair.png");
  background-repeat: no-repeat;
  background-position: center;
